// FF 72 fix. Scrollbars colored like body background.
body
{
  background-color: transparent;
}
#mainBackground
{
	background-color: $red;
}

a.disabled,
.disabled a,
a.btn,
.nav-item.current .nav-link
{
	text-decoration: none;
}

#div4all
{
	max-width: $div4all-max-width;
	margin: 0 auto;
	background-color: $div4all-bg;
	
	@media (min-width: ($div4all-max-width + 20))
	{
		margin-top: 10px;
	}
}

.search-resultsAndError
{
	min-height: 110px;
}
.search-results
{
	dt
	{
		margin-top: ($spacer * 2);
	}
}
.result-text
{
	word-wrap: break-word;
}
.searchintro strong
{
	font-weight: inherit;
}

.search-pagination
{
	margin-bottom: ($spacer * 2);
	margin-top: ($spacer * 2);
	overflow: scroll;

	// ul
	.pagination-list
	{
		@extend .pagination;
		@extend .justify-content-center;
		
		li
		{
			@extend .page-item;
			
			&.disabled a
			{
				color: $gray-300;
			}
			&.active
			{
				a
				{
					@include bold();
					@extend .rounded-pill;
					//border-width: 2px;
					border-color: $success;
				}
			}
			a
			{
				@extend .page-link;
			}
		}
	}
}

.toast-warning
{
	background-color: #fff3cf;
	border: 1px solid #feb;
}
.block-download
{
	background-color: #e2e3e5;
	border: 1px solid $black;
	@extend .my-5;
	padding: $spacer ($spacer * 0.75);
	@extend .text-center;
}
#easycalccheckplus
{
	label
	{
		display: block;
	}
	
	input
	{
		@extend .form-control;
	}
}

#CfCopyright
{
	background-color: $red;
	color: $white;
}

// Dynamic BODY class
.HidePageHeader
{
	.site-description
	{
		display: none;
	}
	@include media-breakpoint-down(xs)
	{
		.div4headteaser
		{
			display: none;
		}
		#SITELOGO
		{
			max-height: 85px;
		}
		.navbar-toggler
		{
			margin: 0 auto;
		}
		.teilenSuchenButtons
		{
			text-align: center !important;
		}
		#CfHeader
		{
			background-color: $red;
		}
	}
	@include media-breakpoint-down(sm)
	{
		#CfMenueOben
		{
			margin-top: ($spacer * 0.75);
		}
	}
}


.articleBody,
.article-list-simple
{
	ul, ol
	{
		padding-left: ($spacer * 0.75);
	}
	li
	{
		margin-bottom: ($spacer * 0.5);
		list-style-position: inside;
	}
	ul > li > ul
	{
		margin-top: ($spacer * 0.5);
		//padding-left: ($spacer * 0.75);
	}
}

.articleStatus
{
	font-size: 1.1rem;
}
.articleStatus_1
{
	@extend .block-danger;
}
.articlesubtitle
{
	@extend .text-muted;
	@extend .small;
	display: block;
	margin-top: ($spacer * 0.5);
}

figure
{
	@extend .p-1;
	@extend .bg-light;
	@extend .border;
	max-width: 480px;
	@extend .d-inline-block;

	img
	{
		width: 100%;
	}

	// Blog leading items
	&.leadItem
	{
		max-width: 600px;
	}
}

.figure-caption {
	font-family: $figure-caption-font-family;
	font-weight: $figure-caption-font-weight;
	line-height: $figure-caption-line-height;
	background-color: $figure-caption-bg;
	text-align: $figure-caption-text-align;
	font-style: $figure-caption-font-style;
	@extend .mt-1;
}
.iconGhsvs
{
	@extend .mt-1;
}

.image_intro
{
	.aWithButtonOverlay
	{
		display: block;
		text-align: center;
		position: relative;
	}
	.centeredButton
	{
  	top: 50%;
  	left: auto;
		right: 3px;
		bottom: auto;
		opacity: 0.9;
  	transform: translateY(-50%);
		position: absolute;
	}
}

// Wenn in .lead zu groß etc.
figcaption
{
	@extend .figure-caption;
	padding: ($spacer * 0.25);
}


.articleIntrotext
{
	@extend .lead;
}

.page_subheading
{
	font-size: 70%;
}

.page-header
{
	@extend .py-3;
	@extend .px-3;
	text-align: center;
	@extend .bg-light;
	@include border-top-dashed();
	@include border-bottom-dashed();
	margin-bottom: $spacer;
}

.page-headerHandschrift
{
	// Im Normalfall ein H1 und darin die Haupt-Seitenüberschrift (span.page_heading_main)
	// plus Unterüberschrift (span.page_subheading).
	.page_heading
	{
		font-family: $font-family-serif;
	}
	.page_heading_main
	{
		letter-spacing: 1px;
	}
}



img
{
	max-width: 100%;
}
.site-description
{
	font-size: 2rem;
	letter-spacing: 1px;
	font-family: $fontFamilyHandschrift;
}


.close
{
	cursor: pointer;
}

.list-item-inside
{
	list-style-position: inside;
}

.readmore .btn
{
	@extend .btn-primary;
}

.spoilerghsvs
{
	margin-bottom: ($spacer * 1.5);
	button
	{
		margin-top: ($spacer * 0.5);
	}
}
.spoilerbody
{
	padding-top: $spacer;
}

.highlight
{
	@extend .alert-warning;
}

// JS driven #system-message-container
.modalFallback
{
	background-color: $white;
	left: 0;
	right: 0;
	position: fixed;
	top: 5%;
	min-width: 80%;
	max-width: 95%;
	margin: 0 auto;
	z-index: $zindex-modal;
}

.invalid
{
	border: 2px solid $danger;
}
label.invalid
{
	padding: 5px;
}
.div4article-info
{
	padding: ($spacer * 1);
	margin-bottom: ($spacer * 1.5);
	margin-top: ($spacer * 0.5);
	border: 1px solid $gray-200;
	border-radius: $border-radius-lg;
	background-color: $gray-200;
}
dl.article-info
{
	font-style: italic;
	margin-bottom: 0;
	dd
	{
		display: inline-block;
		margin-right: ($spacer * 0.25);
		margin-left: 0;
		margin-bottom: 0;
	}
}
.label
{
	@extend .badge;
	margin-bottom: ($spacer * 0.25);
}
a.badge
{
	margin-bottom: ($spacer * 0.5);
	white-space: normal;
}
.label-default
{
	@include badge-variant($gray-600);
	// @extend .badge-dark;
}
.label-categorytag
{
	@include badge-variant($purple);
}

// com_search
.form-group.phrases
{
	select
	{
		@extend .form-control;
	}
}

// footable
.td4link
{
	text-align: center;
}
.td4title .footable-toggle
{
	margin-right: ($spacer * 0.75) !important;
}
.footable-filtering-search
{
	.input-group-btn.open
	{
		.dropdown-menu
		{
			@extend .show;
		}
	}
}

table.footable > thead > tr.footable-filtering > th ul.dropdown-menu > li > a.checkbox input[type="checkbox"]
{
 position: relative;
 margin-left: 0;
 margin-right: ($spacer * 0.25);
}

@include media-breakpoint-up(sm)
{
	.item-page,
	.option-com_search .item-pageLike
	{
		max-width: $text-area-max-width;
		background-color: $white;
		padding: 1rem;
		border: 1px solid rgba(0, 0, 0, 0.15);
		border-radius: 4px;
		@include box-shadow(4px 4px 0px lighten($black, 95%));
	}
	
	.CfMainInner
	{
		@include border-bottom-dashed(transparent);
	}
}
@include media-breakpoint-up(md)
{
	.bg-md-transparent
	{
		background-color: transparent !important;
	}
}

.alert-message
{
	color:#0c5460;
	background-color: #d1ecf1;
}