$h1-font-size: $font-size-base * 2;
$h2-font-size: $font-size-base * 1.75;
$h3-font-size: $font-size-base * 1.5;
$h4-font-size: $font-size-base * 1.25;
$h5-font-size: $font-size-base * 1.1;
$h6-font-size: $font-size-base;




$div4all-bg: $white;
$div4all-max-width: 1600px;
$text-area-max-width: 1024px;

$body-bg: $red;
$body-color: $black;


$code-font-size: 100%;
$pre-color: $black;
$code-bg: #fafafa; // $gray-100;
$kbd-color: $black;
$kbd-bg: $gray-200;

$badge-font-size: 100%;

$list-group-active-bg: $orange;
$list-group-active-border-color: $list-group-active-bg;

$font-family-serif: "Palatino Linotype", "Book Antiqua", Palatino, serif;
$fontFamilyHandschrift: "Just Me Again Down Here", Georgia,  "Times New Roman", cursive;

$headings-font-weight: 600;

$figure-caption-font-size: $font-size-base;
$figure-caption-color: $body-color;
$figure-caption-text-align: center;
$figure-caption-font-weight: $font-weight-base;
$figure-caption-line-height: $line-height-base;
$figure-caption-font-style: italic;
$figure-caption-bg: $yellow-light;
$figure-caption-font-family: $font-family-base;

$link-decoration: underline;
$link-hover-decoration: none;

$card-cap-bg: $gray-100;

// Synchronisere mit "navbar-expand-md" Menü-Klasse!
$nav-collapse-breakpoint: md;

$nav-pills-link-active-color: $black;
$nav-pills-link-active-bg: $orange;

$dropdown-min-width: 12rem;
$dropdown-link-color: $link-color;
// $dropdown-link-hover-color: $red;
// $dropdown-link-hover-bg: lighten($yellow, 15%);
$navbar-default-link-hover-color: $white; //custom
$navbar-default-link-hover-bg: $red; //custom
$navbar-active-font-weight: 600;
//$navbar-padding-y: 0.5rem;

$caret-width:                 .5em;

$alert-padding-x: 0.75rem;

$enable-responsive-font-sizes: true;

// Funktioniert nicht mit node-sass wegen "Error: Import directives may not be used within control directives or mixins".
// $enable-fontawseome-free: true;
// $enable-icomoon2fontawseome-free: false;

$fontawesome-free-path: "../../../media/plg_system_bs3ghsvs/fontawesome-free/5";
$fa-font-path: "#{$fontawesome-free-path}/webfonts";

// Alle Transitions via Bootstrap @include transition(xyz) deaktivieren bedeutet: true
$enable-prefers-reduced-motion-media-query: false;

