// Inline code
code
{
	background-color: $code-bg;
}

// Blocks of code
pre
{
	background-color: $code-bg;
	padding: ($spacer * 0.75);
}
pre {
  word-break: break-all;
  word-wrap: break-word;
  border: 1px solid $gray-400;
}
pre code
{
	white-space: pre-wrap;
