
/* STARTMARKER _text-styles.scss */

.EXCLUDEVENOBOX {
  cursor: inherit;
}
.breakall {
  word-break: break-all;
}
.code-filename {
  font-family: $font-family-monospace;
  background-color: $code-bg;;
	color: darken(blue, 0%);
	font-size: $code-font-size;
}
// Erweiterungsnamen, Software-Namen
.code-eigenname {
	font-style: italic;
}
.code-fachausdruck {
	font-family: $font-family-monospace;
	color: darken(blue, 0%);
	font-style: italic;
}
.code-zeilenverweis {
	@include bold();
	color: darken(#28a745, 15%);
}

.code-inline {
	background-color: $code-bg;
	white-space: pre-wrap;
	font-size: $code-font-size;
	color: $code-color;
}

.text-courier,
.text-monospace {
  font-family: $font-family-monospace;
}
.text-fett,
.text-bold {
  @include bold();
}
.text-fett-blau {
  color: $blue !important;
  @include bold();
}
.text-fett-gruen {
  color: $green !important;
  @include bold();
}
.text-fett-lila,
.text-fett-purple {
  color: $purple !important;
  @include bold();
}
.text-fett-orange {
  color: $orange !important;
  @include bold();
}
.text-fett-rot {
  color: $red !important;
  @include bold();
}
.text-fett-unterstrichen {
  @include bold();
  text-decoration: underline !important;
}
.text-fett-zentriert {
  @include bold();
  text-align: center !important;
}
.text-kursiv,
.text-italic {
  font-style: italic !important;
	// font-weight: 500;
}
.text-large,
.large {
  font-size: 32px !important;
}
.text-large-lineheight {
  font-size: 32px !important;
  line-height: 35.2px !important;
}
.text-lila,
.text-purple {
  color: $purple !important;
}
.text-links
{
  text-align: left !important;
}
.text-medium {
  font-size: 20px !important;
}
.text-medium-lineheight {
  font-size: 20px !important;
  line-height: 22px !important;
}
.text-lineheight-small {
  line-height: 1.1 !important;
}
.text-rot,
.text-red {
  color: $red;
}
.text-smaller {
  font-size: 90%;
}
.text-mini {
  font-size: 65%;
}
.text-unterstrichen,
.text-underline {
  text-decoration: underline !important;
}
.text-zentriert {
  text-align: center !important;
}

@each $color, $value in $theme-colors {
  .block-#{$color} {
    border-color: theme-color(#{$color}) !important;
		background-color: theme-color(light);
		@include block();
  }
}
@each $color, $value in $theme-colors {
  .inline-#{$color} {
		background-color: theme-color-level($color, -10);
  }
}

// Dummies für JCE.
.hyphenate
{
	cursor: default;
}
.donthyphenate
{
	cursor: default;
}
/* ENDMARKER _text-styles.scss */